.superset {
	padding-top: 200rem;

	@media (max-width: 991px) {
		padding-top: 100rem;
	}

	&__title {
		max-width: 550rem;
		text-align: left;

		@media (max-width: 991px) {
			max-width: 320rem;
		}
	}

	&__item {
		position: relative;
		background-image: url('../img/bg-superset.png');
		background-size: 100% 100%;
		background-position: center;
		background-repeat: no-repeat;

		@media (max-width: 991px) {
			background-size: contain;
			margin-top: 50rem;
		}
	}

	&__img {
		width: 1109rem;
		height: 874rem;
		animation: bounce 8s;
		animation-direction: alternate;
		animation-iteration-count: infinite;
		will-change: transform,
			animation;

		@media (max-width: 991px) {
			width: 485rem;
			height: 379rem;
			margin: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__desc {
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		font-weight: 700;
		font-size: 24rem;
		line-height: 24rem;
		text-align: center;
		color: #E4002B;
		z-index: 10;

		@media (max-width: 991px) {
			font-size: 14rem;
			line-height: 14rem;
		}


		&--1 {
			width: 254rem;
			height: 158rem;
			left: 272rem;
			bottom: 60rem;

			@media (max-width: 991px) {
				width: 164rem;
				height: 88rem;
				left: 82rem;
				bottom: -40rem;
			}

			&::before {
				transform: rotate(-15deg);
			}

			span {
				@media (max-width: 991px) {
					max-width: 123rem;
				}
			}
		}

		&--2 {
			width: 265rem;
			height: 159rem;
			top: 50%;
			left: 552rem;
			transform: translateY(-50%);

			@media (max-width: 991px) {
				width: 143rem;
				height: 89rem;
				left: 248rem;
				top: 150rem;
				transform: none;
			}

			&::before {
				transform: rotate(-20deg);
			}

			span {
				max-width: 160rem;

				@media (max-width: 991px) {
					max-width: 94rem;
				}
			}
		}

		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: #F6E81F;
			border-radius: 50%;
			z-index: -1;
		}
	}

	&__price {
		font-weight: 700;
		font-size: 100rem;
		line-height: 100%;
		text-align: right;
		letter-spacing: -0.01em;
		color: #E4002B;

		@media (max-width: 991px){
			font-size: 50rem;
			margin-top: 30rem;
		}

		span {
			font-weight: 400;
			font-size: 25rem;
			line-height: 25rem;
		}
	}
}