html {
	box-sizing: border-box;
	font-size: 0.052083333333333336vw;

	@media (max-width: 991px) {
		font-size: 0.18vw;
	}
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	font-size: 16rem;
	min-width: 320rem;
	font-family: "Korzinka", sans-serif;
}

body.lock {
	overflow: hidden;
}

input {
	border: none;

	&::placeholder {
		opacity: 1;
	}
}

textarea {
	border: none;
	resize: none;

	&::placeholder {
		opacity: 1;
	}
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
	color: #000;
	transition: all 0.3s ease;

	&:hover {
		color: #E4002B;
	}
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.main {
	flex: 1 1 auto;
}

.container {
	margin: 0 auto;
	padding: 0 15rem;
	max-width: 1200rem;

	@media (max-width: 991px) {
		max-width: 750rem;
	}

	@media (max-width: 767px) {
		max-width: none;
	}
}

.title {
	font-size: 55rem;
	line-height: 55rem;
	text-align: center;

	@media (max-width: 991px) {
		font-size: 32rem;
		line-height: 32rem;
	}

	&--black {
		color: #000;
	}

	&--white {
		color: #fff;
	}
}