.partners {
	padding-top: 80rem;

	&__items {
		display: flex;
		justify-content: center;
		margin-top: 30rem;
	}

	&__item {
		width: 160rem;
		height: 160rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__item+&__item {
		margin-left: 30rem;
	}
}