.footer {
	padding: 160rem 0 100rem;

	@media (max-width: 991px) {
		padding: 80rem 0 60rem;
	}

	@media (max-width: 575px) {
		padding: 55rem 0 50rem;
	}

	&__top {
		display: flex;
		justify-content: space-between;
	}

	&__logo {
		width: 420rem;
		height: 65rem;

		@media (max-width: 991px) {
			width: 226rem;
			height: 35rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__desc {
		max-width: 210rem;
		margin-top: 26rem;
		font-size: 28rem;
		line-height: 120%;
		color: #E4002B;

		@media (max-width: 991px) {
			font-size: 24rem;
		}
	}

	&__socials {
		display: flex;

		a {
			img {
				width: 48rem;
				height: 48rem;
				transition: all 0.3s ease;

				&:hover {
					filter: invert(20%);
				}

				@media (max-width: 991px) {
					width: 32rem;
					height: 32rem;
				}
			}
		}

		a+a {
			margin-left: 40rem;

			@media (max-width: 991px) {
				margin-left: 20rem;
			}
		}
	}

	&__list {
		margin-top: 48rem;
		display: flex;
		flex-wrap: wrap;

		@media (max-width: 575px) {
			flex-direction: column;
		}


		li {
			padding: 0 20rem 20rem 0;
		}
	}

	&__link {
		line-height: 18rem;
		text-align: center;
		color: #222025;
		opacity: 0.5;

		&:hover {
			opacity: 1;
		}
	}

	&__bottom {
		margin-top: 80rem;
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			justify-content: center;
		}

		@media (max-width: 575px) {
			justify-content: flex-start;
			flex-direction: column;
		}
	}

	&__box {
		display: flex;

		@media (max-width: 991px) {
			align-items: center;
			margin-right: 25rem;
		}

		@media (max-width: 575px) {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	&__info {
		margin-right: 80rem;

		@media (max-width: 991px) {
			margin-right: 25rem;
		}

		@media (max-width: 575px) {
			margin: 0 0 25rem 0;
		}

		a,
		span {
			font-weight: 700;
			color: #211F2E;
			opacity: 0.4;

			@media (max-width: 991px) {
				font-size: 12rem;
			}

			@media (max-width: 575px) {
				font-size: 16rem;
			}
		}

		a {
			&:hover {
				color: #E4002B;
				opacity: 1;
			}
		}

		p {
			margin-top: 6rem;
			font-size: 12rem;
			color: #211F2E;
			opacity: 0.4;

			@media (max-width: 991px) {
				font-size: 10rem;
			}

			@media (max-width: 575px) {
				font-size: 12rem;
			}
		}
	}

	&__lang {
		.lang__selected {
			align-items: flex-start;
			opacity: 1;

			span {
				color: #222025;
				opacity: .5;

				@media (max-width: 991px) {
					font-size: 12rem;
				}

				@media (max-width: 575px) {
					font-size: 16rem;
				}
			}
		}

		.lang__arrow {
			align-items: flex-start;
			padding: 2rem 0 0 0;

			@media (max-width: 991px) {
				width: 30rem;
				height: 30rem;
			}

			img {
				filter: invert(50%);

				@media (max-width: 991px) {
					width: 12rem;
					height: 12rem;
				}
			}
		}

		.lang__list {
			top: -110rem;

			@media (max-width: 991px) {
				top: -80rem;
			}
		}

		&.active {
			.lang__list {
				top: -85rem;

				@media (max-width: 991px) {
					top: -60rem;
				}
			}

			.lang__selected span {
				color: #E4002B;
				opacity: 1;
			}

			.lang__arrow img {
				filter: invert(0);
			}
		}

		&.lang {
			@media (max-width: 991px) {
				margin-top: 0;
			}
		}
	}

	&__by {
		font-weight: 700;
		color: #211F2E;
		opacity: 0.4;

		@media (max-width: 991px) {
			font-size: 12rem;
		}

		@media (max-width: 575px) {
			font-size: 16rem;
		}

		span {
			text-decoration: underline;
		}
	}
}