.tabs {
	position: relative;
	z-index: 1;

	&__list {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: 48rem;
	}

	&__item {
		position: relative;
		cursor: pointer;
		font-size: 19rem;
		text-align: center;
		transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
		width: 195rem;
		height: 61rem;

		@media (max-width: 991px) {
			font-size: 15rem;
		}


		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			bottom: 10rem;
			left: 10rem;
			z-index: 1;
			pointer-events: none;

			@media (max-width: 991px) {
				bottom: 40rem;
				left: 0;
			}
		}

		&::after {
			content: '';
			background-image: url('../img/svg/button-3.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: 100%;
			position: absolute;
			top: 0;
			left: 0;
			z-index: -1;
			width: 100%;
			height: 100%;
		}

		&:nth-child(1) {
			&::before {
				background-image: url('../img/svg/tabs-1.svg');
				width: 64rem;
				height: 64rem;

				@media (max-width: 991px) {
					width: 44rem;
					height: 44rem;
				}
			}
		}

		&:nth-child(2)::before {
			background-image: url('../img/svg/tabs-2.svg');
			width: 68rem;
			height: 58rem;

			@media (max-width: 991px) {
				width: 45rem;
				height: 45rem;
			}
		}

		&:nth-child(3)::before {
			background-image: url('../img/svg/tabs-3.svg');
			width: 53rem;
			height: 70rem;

			@media (max-width: 991px) {
				width: 33rem;
				height: 43rem;
			}
		}

		&:nth-child(4)::before {
			background-image: url('../img/svg/tabs-4.svg');
			width: 79rem;
			height: 65rem;
			left: -20rem;

			@media (max-width: 991px) {
				width: 49rem;
				height: 40rem;
			}
		}

		@media (max-width: 991px) {
			width: 100%;
			border-radius: 0;
		}

		@media (max-width: 575px) {
			padding-left: 0;
			padding-right: 0;
		}

		&:first-child {
			@media (max-width: 991px) {
				border-radius: 10rem 10rem 0 0;
			}
		}

		&:hover {
			color: #4f4f4f;
		}

		&.active {
			transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
			pointer-events: none;

			&::after {
				background-image: url('../img/svg/button-2.svg');
			}

			.tabs__btn {
				color: #fff;
			}
		}
	}

	&__item+&__item {
		margin-left: 25rem;
	}

	&__content {
		display: none;
		margin-top: 50rem;

		&.active {
			display: block;
		}
	}

	&__btn {
		height: 100%;
		width: 100%;
		text-align: right;
		padding-right: 32rem;

		@media (max-width: 991px) {
			font-size: 14rem;
			padding-right: 0;
			text-align: center;
		}
	}

	&__joykis {
		position: relative;
		display: flex;
		flex-direction: column;
		background-size: 100% 100%;
		background-position: center;
		background-repeat: no-repeat;
		padding: 18rem 0;

		@media (max-width: 991px) {
			background-size: contain;
		}

		.tabs__img--thumbs {
			padding-bottom: 30rem;
		}
	}

	&__img {
		margin: auto;

		&--big {
			width: 804rem;
			height: 804rem;

			@media (max-width: 991px) {
				width: 450rem;
				height: 450rem;
			}

			img {
				animation: bounce 8s;
				animation-direction: alternate;
				animation-iteration-count: infinite;
				will-change: transform,
					animation;
			}
		}

		&--thumbs {
			width: 176rem;
			height: 176rem;

			svg {
				position: absolute;
				top: 0;
				left: 0;
				padding-top: 45rem;
			}

			@media (max-width: 991px) {
				width: 90rem;
				height: 90rem;
			}
		}

		img {
			position: relative;
			width: 100%;
			height: 100%;
			object-fit: contain;
			z-index: 10;
		}
	}

	&__lottie {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 40rem;
		left: 50%;
		transform: translateX(-50%);
		transition: all 0.6s ease;
	}

	&__info {
		position: absolute;
		width: 431rem;
		height: 278rem;
		bottom: 110rem;
		right: 0;
		z-index: 1;
		text-align: center;
		display: flex;
		flex-direction: column;
		justify-content: center;

		@media (max-width: 991px) {
			width: 320rem;
			height: 190rem;
			bottom: 5rem;
		}

		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: #E4002B;
			border-radius: 50%;
			transform: rotate(-10deg);
			z-index: -1;
		}
	}

	&__name {
		font-size: 36rem;
		line-height: 1;
		color: #fff;
		font-weight: 700;

		@media (max-width: 991px) {
			font-size: 22rem;
		}
	}

	&__desc {
		margin-top: 8rem;
		font-size: 15rem;
		line-height: 20rem;
		letter-spacing: -0.01em;
		color: #fff;
		opacity: 0.8;
		padding: 0 30rem;

		@media (max-width: 991px) {
			font-size: 12rem;
		}
	}
}


[data-id="forest"] .tabs__joykis {
	background-image: url('../img/bg-joykis-1.png');
}

[data-id="aquatic"] .tabs__joykis {
	background-image: url('../img/bg-joykis-2.png');
}

[data-id="urban"] .tabs__joykis {
	background-image: url('../img/bg-joykis-3.png');
}

[data-id="air"] .tabs__joykis {
	background-image: url('../img/bg-joykis-4.png');
}

.swiper-button-next,
.swiper-button-prev {
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	@media (max-width: 991px) {
		top: 40%;
	}

	@media (max-width: 575px) {
		display: none;
	}
}

.swiper-button-next {
	background-image: url('../img/svg/arrow-left.svg');
	transform: rotate(180deg);
	right: 275rem;

	@media (max-width: 991px) {
		right: 20rem;
	}
}

.swiper-button-prev {
	background-image: url('../img/svg/arrow-left.svg');
	left: 275rem;

	@media (max-width: 991px) {
		left: 20rem;
	}
}

.swiper-button-next::after,
.swiper-button-prev::after {
	content: none;
}