.intro {
	position: relative;
	background-image: url('../img/bg-desktop.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	height: 1200rem;

	@media (max-width: 991px) {
		background-image: url('../img/bg-tablet.png');
		height: 760rem;
	}

	&::before {
		content: '';
		background-image: url('../img/intro-dividers.png');
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 256rem;

		@media (max-width: 991px) {
			height: 102rem;
		}
	}

	&__content {
		padding-top: 168rem;

		@media (max-width: 991px) {
			padding-top: 105rem;
		}
	}

	&__title {
		font-size: 80rem;
		line-height: 80rem;
		font-weight: 900;
		color: #fff;
		max-width: 800rem;

		@media (max-width: 991px) {
			font-size: 40rem;
			line-height: 40rem;
			max-width: 400rem;
		}
	}

	&__desc {
		max-width: 527rem;
		margin-top: 20rem;
		font-size: 25rem;
		line-height: 32rem;
		letter-spacing: -0.02em;
		color: #fff;
		opacity: 0.8;

		@media (max-width: 991px) {
			font-size: 19rem;
			line-height: 24rem;
			max-width: 397rem;
		}
	}

	&__btn {
		display: none;
		position: relative;
		margin-top: 24rem;
		width: 235rem;
		height: 56rem;

		&::before {
			content: '';
			background-image: url('../img/svg/button-1.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}

		&-link {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: 100%;
			color: #fff;
		}

		@media (max-width: 991px) {
			display: block;
		}
	}

	&__phone {
		width: 690rem;
		height: 347rem;
		position: absolute;
		bottom: 27rem;
		left: 327rem;
		transform: rotate(-15.4deg);
		z-index: 1;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			animation: bounce 8s;
			animation-direction: alternate;
			animation-iteration-count: infinite;
			will-change: transform, animation;
		}

		@media (max-width: 991px) {
			width: 294rem;
			height: 146rem;
			left: 28rem;
			transform: rotate(-5deg);
			bottom: 25rem;
		}

		@media (max-width: 575px) {
			width: 304rem;
			height: 156rem;
			left: 28rem;
			transform: rotate(-14deg);
			bottom: 25rem;
		}
	}

	&__qr {
		width: 188rem;
		height: 189rem;
		position: absolute;
		bottom: 40rem;
		left: 869rem;
		z-index: 1;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			animation: bounce2 5s;
			animation-delay: 7ms;
			animation-direction: alternate;
			animation-iteration-count: infinite;
			will-change: transform,
				animation;

			@media (max-width: 991px) {
				border-radius: 23rem;
			}
		}

		@media (max-width: 991px) {
			width: 110rem;
			height: 110rem;
			left: 258rem;
			transform: rotate(5deg);
			bottom: 15rem;
			z-index: 2;
		}

		@media (max-width: 575px) {
			display: none;
		}
	}

	&__joykis {
		width: 690rem;
		height: 690rem;
		position: absolute;
		left: 883rem;
		bottom: 100rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@media (max-width: 991px) {
			width: 267rem;
			height: 263rem;
			left: 290rem;
			bottom: 100rem;
			z-index: 1;
		}

		@media (max-width: 575px) {
			width: 307rem;
			height: 303rem;
			left: 250rem;
			bottom: 20rem;
		}
	}

	&--uz {
		.intro__title {
			max-width: 100%;
		}
	}
}

@keyframes bounce {
	from {
		-webkit-transform: translate3d(0, -15px, 0);
		transform: translate3d(0, -15px, 0)
	}

	to {
		-webkit-transform: translate3d(0, 15px, 0);
		transform: translate3d(0, 15px, 0)
	}
}