.box {
	background-size: 100% 100%;
	background-position: center;
	background-repeat: no-repeat;
	margin-top: 140rem;
	padding-top: 140rem;

	@media (max-width: 991px){
		margin-top: 80rem;
	}

	&--team {
		background-image: url('../img/bg-box-1.png');

		.box__items {
			@media (max-width: 575px) {
				padding-bottom: 320rem;
			}
		}
	}

	&--howtoplay {
		background-image: url('../img/bg-box-2.png');

		.box__desc {
			margin-top: 0;
		}
	}

	@media (max-width: 991px) {
		background-size: cover;
		padding-top: 80rem;
	}

	&__title {
		max-width: 700rem;
		margin: auto;

		@media (max-width: 991px) {
			max-width: 400rem;
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-top: 90rem;


		@media (max-width: 575px) {
			position: relative;
			margin-top: 50rem;
		}
	}

	&__item {
		flex: 0 0 40%;
		text-align: center;

		@media (max-width: 575px) {
			flex: 0 0 100%;
		}

		&:nth-child(2n) {
			@media (min-width: 576px) {
				margin-top: 120rem;
			}
		}

		&--plane {
			@media (max-width: 575px) {
				position: absolute;
				bottom: 0;
				left: 0;
			}

			.box__img {
				width: 697rem;
				height: 627rem;
				animation: bounce 8s;
				animation-direction: alternate;
				animation-iteration-count: infinite;
				will-change: transform, animation;

				@media (max-width: 991px) {
					width: 247rem;
					height: 274rem;
				}
			}
		}

		&--coins {
			@media (max-width: 991px) {
				display: flex;
			}

			@media (max-width: 575px) {
				position: absolute;
				bottom: 70rem;
				right: 10rem;
			}

			.box__img {
				@media (max-width: 991px) {
					width: 228rem;
					height: 114rem;
				}
			}
		}

		&--car {
			flex: 0 0 100%;
			margin-top: 0 !important;

			.box__img {
				width: 692rem;
				height: 692rem;

				@media (max-width: 991px) {
					width: 341rem;
					height: 292rem;
				}
			}
		}
	}

	&__item+&__item {
		@media (max-width: 991px) {
			margin-top: 43rem;
		}
	}

	&__img {
		width: 427rem;
		height: 397rem;
		margin: auto;

		@media (max-width: 991px) {
			width: 224rem;
			height: 234rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__desc {
		max-width: 437rem;
		margin: 26rem auto 0;
		font-size: 25rem;
		line-height: 32rem;
		letter-spacing: -0.02em;
		color: #fff;

		@media (max-width: 991px) {
			margin-top: 16rem;
			font-size: 18rem;
			line-height: 24rem;
			letter-spacing: -0.01em;
		}

		@media (max-width: 575px) {
			max-width: 318rem;
		}
	}

	&__btn {
		display: none;
		position: relative;
		width: 221rem;
		height: 44rem;
		margin: auto;

		@media (max-width: 991px) {
			display: block;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/button-4.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}

		&-link {
			width: 100%;
			height: 100%;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			color: #222025;
			font-size: 14rem;
			margin: 20rem auto 0;
			text-align: center;
		}
	}
}