.faq {
	padding-top: 140rem;

	&__content {
		max-width: 998rem;
		margin: 30rem auto 0;

		@media (max-width: 991px) {
			padding: 0 15rem;
		}
	}

	&__item {
		border-top: 1rem solid #DCDCDF;
		border-bottom: 1rem solid #DCDCDF;

		&:first-child {
			border-top: none;
		}
	}

	&__title {
		position: relative;
		font-weight: 700;
		font-size: 25rem;
		line-height: 28rem;
		color: #222025;
		padding: 25rem 0;
		cursor: pointer;

		@media (max-width: 991px) {
			font-size: 19rem;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/arrow-faq.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			right: 5rem;
			transform: translateY(-50%);
			width: 16rem;
			height: 10rem;
			transition: all 0.3s ease;
		}

		&.active {
			&:before {
				transform: rotate(180deg);
			}
		}
	}

	&__body {
		display: none;
		padding-bottom: 20rem;
		line-height: 18rem;
		color: #ADADB0;
	}
}