@font-face {
  font-family: "Korzinka";
  src: url("../fonts/../fonts/Korzinka-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal; }

@font-face {
  font-family: "Korzinka";
  src: url("../fonts/../fonts/Korzinka_044-DisplayBoldItalic.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal; }

@font-face {
  font-family: "Korzinka";
  src: url("../fonts/../fonts/Korzinka_044-DisplayExtraBoldItalic.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal; }

html {
  box-sizing: border-box;
  font-size: 0.052083333333333336vw; }
  @media (max-width: 991px) {
    html {
      font-size: 0.18vw; } }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-size: 16rem;
  min-width: 320rem;
  font-family: "Korzinka", sans-serif; }

body.lock {
  overflow: hidden; }

input {
  border: none; }
  input::placeholder {
    opacity: 1; }

textarea {
  border: none;
  resize: none; }
  textarea::placeholder {
    opacity: 1; }

img {
  max-width: 100%; }

a {
  text-decoration: none;
  color: #000;
  transition: all 0.3s ease; }
  a:hover {
    color: #E4002B; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

button {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0; }

.wrapper {
  position: relative;
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column; }

.main {
  flex: 1 1 auto; }

.container {
  margin: 0 auto;
  padding: 0 15rem;
  max-width: 1200rem; }
  @media (max-width: 991px) {
    .container {
      max-width: 750rem; } }
  @media (max-width: 767px) {
    .container {
      max-width: none; } }

.title {
  font-size: 55rem;
  line-height: 55rem;
  text-align: center; }
  @media (max-width: 991px) {
    .title {
      font-size: 32rem;
      line-height: 32rem; } }
  .title--black {
    color: #000; }
  .title--white {
    color: #fff; }

