.header {
	position: absolute;
	top: 40rem;
	left: 0;
	width: 100%;
	z-index: 10;

	@media (max-width: 991px) {
		padding: 0 15rem;
		top: 20rem;
	}

	&__container {
		max-width: 1800rem;
		margin: auto;
	}

	&__content {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__logo {
		position: relative;
		z-index: 10;
		display: block;
		width: 300rem;
		height: 45rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@media (max-width: 991px) {
			width: 200rem;
			height: 30rem;
		}
	}
}

.lang {
	position: relative;

	@media (max-width: 991px) {
		margin-top: 20rem;
	}

	&.active {
		.lang__selected {
			opacity: 1;
		}

		.lang__list {
			opacity: 1;
			visibility: visible;
			top: 50rem;
		}

		.lang__arrow img {
			transform: rotate(180deg);
		}
	}

	&__selected {
		display: flex;
		align-items: center;
		color: #fff;
		opacity: 0.7;
		transition: all 0.3s ease;

		@media (max-width: 991px) {
			opacity: 1;
			justify-content: center;
		}
	}

	&__arrow {
		width: 40rem;
		height: 40rem;
		display: flex;
		align-items: center;
		justify-content: center;
		padding-left: 3rem;
		cursor: pointer;

		img {
			width: 16rem;
			height: 16rem;
			transition: all 0.3s ease;
		}
	}

	&__list {
		position: absolute;
		top: 60rem;
		left: 50%;
		transform: translateX(-50%);
		transition: all 0.3s ease;
		padding: 25rem;
		background-color: #fff;
		box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
		border-radius: 8rem;
		opacity: 0;
		visibility: hidden;

		@media (max-width: 991px) {
			padding: 15rem;
		}
	}

	&__item+&__item {
		margin-top: 16rem;
	}

	&__link {
		color: #222025;
	}
}


.menu {
	&__body {
		@media (max-width: 991px) {
			background-color: #26B0D7;
			position: fixed;
			display: flex;
			flex-direction: column;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding: 90rem 20rem 20rem;
			opacity: 0;
			visibility: hidden;
			transition: all .3s ease 0s;
			text-align: center;
			z-index: 2;

			&:before {
				content: "";
				background-color: #26B0D7;
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				height: 70rem;
				z-index: 10;
			}

			&.active {
				opacity: 1;
				visibility: visible;
			}
		}

		&.active {
			@media (max-width: 991px) {
				.menu__list {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}

	&__list {
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}

		&>li {
			@media (max-width: 991px) {
				border-bottom: 1rem solid #ccc;

				&:first-child {
					border-top: 1rem solid #ccc;
				}
			}
		}

		&>li+li {
			@media (min-width: 992px) {
				margin-left: 30rem;
			}
		}
	}

	&__link {
		color: #fff;

		@media (max-width: 991px) {
			display: block;
			padding: 20rem 0;
		}
	}

	&__icon {
		@media (max-width: 991px) {
			z-index: 5;
			display: block;
			position: relative;
			width: 35rem;
			height: 20rem;
			cursor: pointer;

			span,
			&:before,
			&:after {
				left: 0;
				position: absolute;
				height: 10%;
				width: 100%;
				transition: all 0.3s ease 0s;
				background-color: #fff;
				border-radius: 10rem;
			}

			&:before,
			&:after {
				content: "";
			}

			&:before {
				top: 0;
			}

			&:after {
				bottom: 0;
			}

			span {
				top: 50%;
				transform: scale(1) translate(0, -50%);
			}

			&.active span {
				transform: scale(0) translate(0, -50%);
			}

			&.active:before {
				top: 50%;
				transform: rotate(-45deg) translate(0, -50%);
			}

			&.active:after {
				bottom: 50%;
				transform: rotate(45deg) translate(0, 50%);
			}
		}
	}
}