.joykis {
	padding-top: 160rem;
	padding-top: 70rem;

	&__desc {
		max-width: 1200rem;
		margin: 25rem auto 0;
		font-size: 25rem;
		line-height: 32rem;
		text-align: center;
		letter-spacing: -0.02em;
		color: #222025;

		@media (max-width: 991px) {
			font-size: 16rem;
			line-height: 24rem;
			padding: 0 15rem;
		}
	}

	&__tabs {
		margin-top: 70rem;
	}

	&__swiper {
		&-thumbs {
			margin-top: 40rem;

			.swiper-wrapper {
				align-items: center;
			}

			.swiper-slide {
				width: 16%;
				height: 100%;
				cursor: pointer;
			}

			.swiper-slide-thumb-active {
				opacity: 1;
			}
		}
	}

	&--uz {
		.tabs__item:nth-child(4)::before {
			left: 0;
		}

		.tabs__btn {
			padding-right: 45rem;

			@media (max-width: 991px) {
				padding-right: 0;
			}
		}

		.tabs__info {
			width: 370rem;
			height: 220rem;
		}
	}
}